* {
  margin: 0px;
  padding: 0px;
}

/* Brand name "Will Utterback values */
.navbar-light .navbar-brand {
  font-size: 30px;
  color: #e09e9e;
}

/* About Me, Portfolio, Contact me values */
.navbar-light .navbar-nav .nav-link {
  color: #e09e9e;
  font-size: 20px;
}
/* Toggle - removed border due to color problems */
.navbar-light .navbar-toggler {
  border: 0;
}

/* navbar item values, the button */
.l-navbar {
  width: 60px;
  display: block;
  margin: 15x auto;
  border: 4px solid #e09e9e;
  padding: 6px 8px;
}

/* More navbar stuff, the button */
.l-navbar div {
  height: 5px;
  background: #0c0b0b;
  margin: 7px 0px 7px 0px;
  border-radius: 25px;
}

/* all line classes are for button */
.line1 {
  width: 15px;
}

.line2 {
  width: 20px;
}

.line3 {
  width: 25px;
}

/* button line animation */
.dropdown:hover div {
  width: 30px;
  transition: 0.5s;
}

h2 {
  font-size: 24px;
}

/* Container values for all 3 pages */
.container {
  padding: 30px;
  background-color: rgb(255, 229, 196);
}

/* Color required for accessibility */
.btn-primary {
  background-color: navy;
}

.footer {
  left: 0;
  bottom: 0;
  height: 20px;
  line-height: 18px;
  width: 100%;
  background-color: #343a40;
  color: white;
}

/* Background javascript stuff */
#tsparticles canvas {
  height: 100%;
  background: navy;
  position: fixed;
  top: 0;
  left: 0;
}
